import React, { Component } from 'react';
import marked from 'marked';

marked.setOptions({
  //renderer: new marked.Renderer(),
	// gfm: true,
	// tables: true,
	breaks: true,
	// pedantic: false,
	// sanitize: false,
	// smartLists: true,
	// smartypants: false
});
const renderer = new marked.Renderer();

// Override function
renderer.link = function(href, title, text) {
  return `
      <a target="_blank" title=${title} href=${href}>${text}</a>`;
};

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      content: "# Markdown Previewer\n## React + Marked\nThis project is part of the [freeCodeCamp] **Front End Libraries Projects**.\n\nWant to see an image?\n\n![React logo](favicon.ico)\n\nIt includes:\n+ an editor\n+ and a preview...\n\nHere's some inline `code`.\n\nThis is how to open link in a new tab:\n~~~\nreturn 'nah, you'll have to figure that out yourself';\n // check Extensibility on Marked doc though :) ~~~\nAnd that's it! Wait, almost forgot the quote.\n> If only I had a random quote generator...\n\n[freeCodeCamp]: https://learn.freecodecamp.org/"
    };
    this.handleChange = this.handleChange.bind(this);
  }

  // a header (H1 size), a sub header (H2 size), a link, inline code, a code block, a list item, a blockquote, an image, and bolded text.

  handleChange(event) {
    this.setState({
      content: event.target.value
    })
  }

  render() {
    return (
      <div id="app">
        <textarea id="editor" cols="70" rows="15" value={this.state.content} onChange={this.handleChange}/>
        <div id="preview" dangerouslySetInnerHTML={{__html: marked(this.state.content, {renderer: renderer})}} />
      </div>
    );
  }
}

export default App;



